import * as THREE from "three";
import GUI from "lil-gui";
import { EffectComposer, EffectPass, RenderPass } from "postprocessing";
import { CurveEffect } from "./fx/CurveEffect";

const meshes = [];

// Set to true to update meshes on scroll instead of animation frame
// Be aware of performance implications
let updateOnScroll = false;

/**
 * Utils
 */

/**
 * getOrthographicCameraFrustum
 * Calculates the camera's left, right, top, and bottom properties for an orthographic camera
 * based on the browser viewport size and a desired scene size.
 *
 * @param {number} sceneSize - The desired size of the scene in Three.js units.
 * @returns {Object} - An object with the following properties:
 *   - left: The left edge of the camera's view frustum.
 *   - right: The right edge of the camera's view frustum.
 *   - top: The top edge of the camera's view frustum.
 *   - bottom: The bottom edge of the camera's view frustum.
 */
function getOrthographicCameraFrustum(sceneSize) {
	// Get the size of the browser viewport
	const viewportWidth = window.innerWidth;
	const viewportHeight = window.innerHeight;

	// Calculate the aspect ratio
	const aspectRatio = viewportWidth / viewportHeight;

	let cameraLeft, cameraRight, cameraTop, cameraBottom;

	if (aspectRatio >= 1) {
		// Landscape orientation
		cameraLeft = -sceneSize;
		cameraRight = sceneSize;
		cameraTop = sceneSize / aspectRatio;
		cameraBottom = -sceneSize / aspectRatio;
	} else {
		// Portrait orientation
		cameraLeft = -sceneSize * aspectRatio;
		cameraRight = sceneSize * aspectRatio;
		cameraTop = sceneSize;
		cameraBottom = -sceneSize;
	}

	return {
		left: cameraLeft,
		right: cameraRight,
		top: cameraTop,
		bottom: cameraBottom,
	};
}

/**
 * getElementSpecsNormalized
 * @param {HTMLElement} el
 * @returns {Array<number>} [x, y, w, h, worldX, worldY]
 */
const getElementSpecsNormalized = (el) => {
	const elRect = el.getBoundingClientRect();
	const elCenter = {
		x: elRect.left + elRect.width / 2,
		y: elRect.top + elRect.height / 2 + window.scrollY, // Add scroll offset
	};
	const _x = (elCenter.x / window.innerWidth) * 2 - 1;
	const _y = -((elCenter.y - window.scrollY) / window.innerHeight) * 2 + 1; // Subtract scroll offset
	const w = (elRect.width / window.innerWidth) * 2 + 1;
	const h = (elRect.height / window.innerHeight) * 2 + 1;

	const elVecWorld = new THREE.Vector3(_x, _y, 0).unproject(camera);
	const { x, y } = elVecWorld;

	return [x, y, w, h];
};

/**
 * setMeshSpecs
 * @param {THREE.Mesh} mesh
 * @param {number} x
 * @param {number} y
 * @param {number} w
 * @param {number} h
 */
const setMeshSpecs = (mesh, x, y, w, h) => {
	const { width, height } = getCanvasSizeInThreeUnits(camera, renderer);

	mesh.current.position.x = x;
	mesh.current.position.y = y;

	const scaled = (m, n) => (m * (1 - Math.abs(n))) / 2;
	let sw = scaled(width, w);
	let sh = scaled(height, h);

	mesh.current.scale.x = sw;
	mesh.current.scale.y = sh;
};

/**
 * getCanvasSizeInThreeUnits
 * @param {THREE.Camera} camera
 * @param {THREE.Renderer} renderer
 * @returns {{width: number, height: number}}
 */
function getCanvasSizeInThreeUnits(camera, renderer) {
	// Get the size of the canvas in pixels
	const canvasSize = new THREE.Vector2();
	renderer.getSize(canvasSize);

	// Calculate the size of the canvas in Three.js units
	let canvasWidthInUnits, canvasHeightInUnits;

	if (camera.isPerspectiveCamera) {
		canvasWidthInUnits =
			2 *
			Math.tan(THREE.MathUtils.degToRad(camera.fov / 2)) *
			camera.position.z;
		canvasHeightInUnits = canvasWidthInUnits / camera.aspect;
	} else if (camera.isOrthographicCamera) {
		canvasWidthInUnits = camera.right - camera.left;
		canvasHeightInUnits = camera.top - camera.bottom;
	} else {
		throw new Error(
			"Camera must be either a PerspectiveCamera or an OrthographicCamera"
		);
	}

	return {
		width: canvasWidthInUnits,
		height: canvasHeightInUnits,
	};
}

/**
 * initSceneElements
 * Initialize scene elements by creating meshes for each element with data-scene attribute
 * Each mesh is created with a plane geometry and basic material, and positioned based on the element's position
 * @returns {Array<{current: THREE.Mesh, specs: Array<number>}>} Array of mesh objects with their specifications
 */
const initSceneElements = () => {
	sceneEls.forEach((el, index) => {
		const specs = getElementSpecsNormalized(el);

		// Create mesh
		const mesh = {
			current: new THREE.Mesh(
				new THREE.PlaneGeometry(1, 1),
				new THREE.MeshBasicMaterial()
			),
			specs,
			domElement: el, // Store reference to DOM element
		};

		// Find asset
		const asset = el.querySelector("[data-scene-asset]");
		const elementType = asset?.tagName;

		if (asset && ["VIDEO", "IMG"].includes(elementType)) {
			const src = asset.currentSrc;

			if (src) {
				// Get element aspect ratio
				const elAspect = el.clientWidth / el.clientHeight;

				let texture, width, height;

				if (elementType === "VIDEO") {
					texture = new THREE.VideoTexture(asset);
					({ videoWidth: width, videoHeight: height } = asset);
				} else if (elementType === "IMG") {
					const loader = new THREE.TextureLoader();
					texture = loader.load(src, texture);
					({ naturalWidth: width, naturalHeight: height } = asset);
				}

				applyTexture(mesh, texture, width, height, elAspect);
			}
		}

		setMeshSpecs(mesh, ...mesh.specs);
		scene.add(mesh.current);
		meshes.push(mesh);
	});

	return meshes;
};

/**
 * Apply and scale material texture
 * @param {THREE.Mesh} mesh
 * @param {THREE.Texture} texture
 * @param {number} twidth
 * @param {number} theight
 * @param {number} fitaspect
 */
const applyTexture = (mesh, texture, twidth, theight, fitaspect) => {
	texture.flipY = false;
	texture.colorSpace = THREE.SRGBColorSpace;
	texture.wrapS = THREE.RepeatWrapping;
	texture.repeat.x = -1;

	const aspect = twidth / theight;

	// offset and recentre texture
	if (fitaspect > aspect) {
		texture.repeat.y = aspect / fitaspect;
		texture.offset.y = (1 - texture.repeat.y) / 2;
	} else if (fitaspect < aspect) {
		texture.repeat.x = (fitaspect / aspect) * -1;
		texture.offset.x = (1 - texture.repeat.x) / 2;
	}

	mesh.current.material.map = texture;
	mesh.current.material.transparent = true;
};

/**
 * positionSceneElements
 * Position scene meshes based on position of DOM elements
 */
const positionSceneElements = () => {
	sceneEls.forEach((el) => {
		const mesh = meshes.find((m) => m.domElement === el);
		mesh.specs = getElementSpecsNormalized(el);
		setMeshSpecs(mesh, ...mesh.specs);
	});
};

/**
 * Setup
 */

// Scene DOM elements
const sceneEls = document.querySelectorAll("[data-scene]");

// Debug
const gui = new GUI();

// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();

/**
 * Sizes
 */
const sizes = {
	width: window.innerWidth,
	height: window.innerHeight,
};

/**
 * Camera
 */

// Get the camera frustum
const cameraFrustum = getOrthographicCameraFrustum(10);

// Create the camera
const camera = new THREE.OrthographicCamera(
	cameraFrustum.left,
	cameraFrustum.right,
	cameraFrustum.top,
	cameraFrustum.bottom,
	0.1,
	1000
);

camera.position.set(0, 0, 10);
scene.add(camera);

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
	canvas: canvas,
	powerPreference: "high-performance",
	antialias: false,
	stencil: false,
	depth: false,
	alpha: true,
});
renderer.setSize(sizes.width, sizes.height);
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

/**
 * Post processing
 */

const composer = new EffectComposer(renderer);
composer.addPass(new RenderPass(scene, camera));
const curveEffect = new CurveEffect();
composer.addPass(new EffectPass(camera, curveEffect));

gui.add(curveEffect.uniforms.get("uDistance"), "value").min(0).max(100).step(0.01).name("Distance");
gui.add(curveEffect.uniforms.get("uStrength"), "value").min(0).max(1).step(0.01).name("Strength");

/**
 * Animate
 */
const tick = () => {
	// Render
	composer.render();

	// Update meshes
	if (!updateOnScroll) positionSceneElements();

	// Call tick again on the next frame
	window.requestAnimationFrame(tick);
};

/**
 * Init
 * @param {boolean} updateOnScroll
 * @param {Object} uniforms
 *  - distance: number
*   - strength: number
 */
const init = (updateOnScroll = false, uniforms = {}) => {
	// Set updateOnScroll flag
	updateOnScroll = updateOnScroll;

	// Set uniforms
	curveEffect.uniforms.get("uDistance").value = uniforms.distance;
	curveEffect.uniforms.get("uStrength").value = uniforms.strength;

  // Initialise scene elements
	initSceneElements();

	/** Handle resize */
	window.addEventListener("resize", () => {
		// Update sizes
		sizes.width = window.innerWidth;
		sizes.height = window.innerHeight;

		// Update meshes
		positionSceneElements();

		// Update renderer
		renderer.setSize(sizes.width, sizes.height);
		renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));

		// Update effect composer
		composer.setSize(sizes.width, sizes.height);
	});

	/**
	 * Handle scroll
	 */
	window.addEventListener("scroll", () => {
		// Update meshes		
		if (updateOnScroll) positionSceneElements();
	});

  const start = () => {
    tick();
  }

  return { start };
}

export { init };